<template>
  <!--  -->
  <div>
  <!--  -->
		<div class="cover">
			<img :src="sCover">
			<div class="pictures" @click="showChange('modal-img')"></div>
		</div>
		<div class="main clear">
			<div class="main_left">
			<!--  -->
				<div class="backToTop transition1 rotateY" @click="scrollUpFun()">
          <img src="static/toup2.png">
        </div>
			<!--  -->
				<div class="located">
					<ul>
						<li style="border-bottom: 3px solid #FFDE3A;" @click="scrollFun(500)"></li>
						<li @click="scrollFun(1500)"></li>
						<li @click="scrollFun(2050)"></li>
					</ul>
				</div>
			<!--   -->
				<div class="namecon">
					<div class="na_sc clear">
						<div class="spo_name">{{sName}}</div>
						<div class="spo_score">
							<ul>
                <li v-for="(oneScoreImg, index) in remarkScore" :key="index">
                  <img :src="oneScoreImg">
                </li>
						  </ul>
							<div>{{sScore}}</div>
						</div>
					</div>
					<div class="short_addr clear">{{sBelongTo}}</div>
				</div>
			<!--  -->
				<div class="introduce clear">
					<ul>
						<li></li>
						<li>{{sIntroduction}}</li>
					</ul>
				</div>
			<!--  -->
				<div class="contact clear">
					<ul>
						<li></li>
						<li>{{sContact}}</li>
					</ul>
				</div>
			<!--  -->
        <div class="spo_video clear">
          <video width="100%" height="100%" controls>
            <source :src="sVideo"  type="video/mp4">
          </video>
        </div>
				<div class="hengg1 clear"></div>
			<!--  -->
				<div class="address clear">
					<ul>
            <li></li>
            <li>{{sAddress}}</li>
            <span style="color:#aaa;text-decoration: underline;font-size: small;cursor: pointer;" @click="searchPleace(sName)"></span>
					</ul>
				</div>
			<!--  -->
				<div class="map">
          <div id="container" style="width: 840px;height: 450px"></div>
        </div>
				<div class="hengg1 clear"></div>
			</div>

		<!--  -->
			<div class="main_right">
				<div class="price">{{priceName2}}&nbsp; &nbsp; <span style="font-size:20px;font-weight:bold;">{{sTicket}}RMB</span>&nbsp; &nbsp; {{priceName3}}</div>
				<div class="support">
					<div class="sup_btn" v-if="good_show === 'good'" @click="ChangeGood('gooded', 1)">
						<img src="static/icons/good.png">
						
					</div>
					<div class="sup_btn" v-if="good_show === 'gooded'" @click="ChangeGood('good', -1)">
						<img src="static/icons/gooded.png">
						
					</div>
					<div class="sup_count">
						<span>{{sLikers}}</span>
						
					</div>
					<div class="share">
						<img src="static/icons/wechated.png">
						<img src="static/icons/sinaed.png">
						<img src="static/icons/mailed.png">
						<img src="static/icons/pointes.png">
					</div>
				</div>
				<div class="wrong">
					<img src="static/icons/send.png">
					<router-link :to="{ path: '/show/finderror' }" class="aFont" target="_blank">{{priceName}}</router-link>
				</div>
			</div>
		</div>
		<!---->
    <div class="modal" v-if="showModalValue === 'modal-img'">
      <button class="btn-close" @click="showChange('close')"></button>
      <div class="modal-con modal-con-noColor">
        <div class="modal-con-img">
          <div class="modal-img-btn modal-img-btn-left" @click="showBefore()" @mouseenter="showBtn()" @mouseleave="showBtn()" :class="{showBtnClass: showButtonValue === true}"></div>
          <img v-for="(image, index) in imgInfo" :src="image" v-if="showImgIndex === index">
          <div class="modal-img-btn modal-img-btn-right" @click="showAfter()"  @mouseenter="showBtn()" @mouseleave="showBtn()" :class="{showBtnClass: showButtonValue === true}"></div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        good_show: 'good',
        scroll: 0,
        startLocation: 0,
        showModalValue: '',
        showButtonValue: false,
        showImgIndex: 0,    /*  */
        // 
        imgInfo: [],
        sScore: 4.0,
        // 
        sBelongTo: '',
        // 
        sIntroduction: '',
        // 
        sContact: '',
        // 
        sAddress: '',
        // 
        sTicket: '',
        sLikers: 0,
        // 
        sCover: '',
        // 
        sVideo: 'static/mp4_video.mp4',
        showbackToTop: false,
        /* (wuyong) */
        dataQianTai: {
          name: '',
          jianjie: '',
          price: '',
          phone: '',
          adress: '',
          fengmian: '',
          images: '',
          video: '',
          location: ''
        },
        // 
        sName: '',
        // 
        priceName: '',
        priceName2: '',
        priceName3: '',
        remarkScore: [],
        // axiosurl
        surl: '',
        sendId : {
          id: ''
        },
        // 
        PlaceSearch: {},
        //  
        scrollIndex: 1
      }
    },
    created () {
      this.menun()
      this.changeFlo()
      if (this.GetQueryString('type') === 'sight') {
        this.priceName = ''
        this.priceName2 = ''
        this.priceName3 = ''
        this.surl = 'ScenicSpot/detail'
      } else if (this.GetQueryString('type') === 'canguan') {
        this.priceName = ''
        this.priceName2 = ''
        this.priceName3 = ''
        this.surl = 'restaurant/detail'
      } else if (this.GetQueryString('type') === 'hotle') {
        this.priceName = ''
        this.priceName2 = ''
        this.priceName3 = ''
      } else {
        this.priceName = ''
        this.priceName2 = ''
        this.priceName3 = ''
        this.surl = 'Hotel/detail'
      }
      this.getData()
    },
    methods: {
      ChangeGood (str, n) {
        this.good_show = str
        this.sLikers += n
      },
      menun () {
        window.scrollTo(0, 0)
      },
      scrollFun (num) {
        let n = Math.floor(num / 10) * 10
        let timer = setInterval(() => {
          if (this.scroll < n) {
            window.scrollTo(this.scroll, this.scroll + 10)
            this.scroll += 10
          } else {
            if (this.scroll < num) {
              window.scrollTo(this.scroll, num)
            }
            clearInterval(timer)
          }
        }, 1)
      },
      scrollUpFun () {
        this.startLocation = this.scroll
        let n = this.startLocation - Math.floor(this.startLocation / 10) * 10
        let timer = setInterval(() => {
          if (this.scroll > n) {
            window.scrollTo(this.scroll, this.scroll - 10)
            this.scroll -= 10
          } else {
            if (this.scroll > 0) {
              window.scrollTo(n, 0)
            }
            clearInterval(timer)
          }
        }, 1)
      },
      menu () {
        this.scroll = document.documentElement.scrollTop || document.body.scrollTop
        // 
        var btnBacktop = document.getElementsByClassName('backToTop')[0]
        if (this.scroll < 500) {
          btnBacktop.style.opacity = '0'
        } else {
          btnBacktop.style.opacity = '1'
        }
        // 
        if (this.scroll > 650 && this.scrollIndex === 1) {
          if (this.scrollIndex === 1) {
            this.scrollIndex = 0
            console.log(this.sName + '' + this.scrollIndex)
          }
          if (this.sName !== '') {
            console.log('')
            console.log(this.sName)
            this.searchPleace(this.sName)
          } else {
            console.log('')
            this.searchPleace('')
          }
        }
      },
      showChange (obj) {
        if (obj === 'modal-img') {
          this.showModalValue = 'modal-img'
        } else if (obj === 'modal-text') {
          this.showModalValue = 'modal-text'
        } else {
          this.showModalValue = ''
        }
      },
      showBtn () {
        this.showButtonValue = !this.showButtonValue
      },
      showBefore () {
        if (this.showImgIndex === 0) {
          this.showImgIndex = this.imgInfo.length - 1
        } else {
          this.showImgIndex -= 1
        }
      },
      showAfter () {
        if (this.showImgIndex === this.imgInfo.length - 1) {
          this.showImgIndex = 0
        } else {
          this.showImgIndex += 1
        }
      },
      /* url */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      //  res.data.info 
      getData () {
        console.log('')
        this.sendId.id = this.GetQueryString('id')
        console.log('id' + this.sendId.id)
        console.log('surl' + this.surl)
        const self= this
        axios.post(self.surl, {id: self.sendId.id}, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }).then(function (res) {
          console.log('')
          self.imgInfo = res.data.pices.split(';')
          if (self.GetQueryString('type') === 'sight') {
            self.sName = res.data.name
            self.sTicket = res.data.price
            self.sCover = 'img/scienspot/cover/' + res.data.cover
            self.sContact = res.data.cinformation
            self.sIntroduction = res.data.synosis
            self.sVideo = 'img/scienspot/videos' + res.data.video
            self.imgInfo.forEach(function (value, index) {
            self.imgInfo[index] = 'img/scienspot/otherPic/' + value
            })
          } else if (self.GetQueryString('type') === 'canguan') {
            self.sName = res.data.name
            self.sTicket = res.data.perprice
            self.sCover = 'img/restaurant/cover/' + res.data.cover
            self.sContact = res.data.phone
            self.sIntroduction = res.data.introduce
            self.sVideo = '' + res.data.video
            self.imgInfo.forEach(function (value, index) {
            self.imgInfo[index] = 'img/restaurant/otherPic/' + value
            })
          } else if (self.GetQueryString('type') === 'hotle') {
            self.sName = res.data.hotelname
            self.sTicket = res.data.lowprice
            self.sCover = 'img/hotel/cover/' + res.data.mainpic
            self.sContact = res.data.phone
            self.sIntroduction = res.data.introduce
            self.sVideo = '' + res.data.video
            self.imgInfo.forEach(function (value, index) {
            self.imgInfo[index] = 'img/hotel/otherPic/' + value
            })
          } else {
            console.log('urltype')
          }
          self.sBelongTo = res.data.location
          self.sAddress = res.data.address
          console.log('')
          console.log('')
          console.log(self.sName)
          console.log('')
          console.log(self.sIntroduction)
          console.log('')
          console.log(self.sBelongTo)
          console.log('')
          console.log(self.imgInfo)
          console.log('')
          console.log(self.sContact)
          console.log('')
          console.log(self.sAddress)
          console.log('')
          console.log(self.sTicket)
          console.log('')
          console.log(self.sCover)
          console.log('')
          console.log(self.sVideo)
        }).catch(function (err) {
          console.log('')
          console.log(err)
        })
      },
      /* 0. */
      changeFlo () {
        var m = parseInt(this.sScore)
        var n = parseInt((this.sScore - m) * 10)
        for (var i = 0; i < m; i++) {
          this.remarkScore[i] = 'static/flo_s.png'
        }
        if (m < 5) {
          this.remarkScore[m] = 'static/flo_k' + n + '.png'
        }
        for (var j = m + 1; j < 5; j++) {
          this.remarkScore[j] = 'static/flo_k.png'
        }
      },
      /*  */
      searchPleace(pleace) {
        this.placeSearch.search(pleace);
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
      /*  */
      var map = new AMap.Map("container", {
        resizeEnable: true
      });
      const self = this;
      AMap.service(["AMap.PlaceSearch"], function() {
        var placeSearch = new AMap.PlaceSearch({ //
          pageSize: 1,
          pageIndex: 1,
          city: "", //
          map: map,
        });
        //
        self.placeSearch = placeSearch;
      });
    }
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import '../../styles/srh.css';
@import '../../styles/model.css';
@import '../../styles/move.css';
</style>



