<template>
  <div class="warp">
    <!-- & -->
    <div class="topbar">
      <div class="topimg imgcenter">
        <img src="static/view.jpg" alt="">
      </div>
      <div class="topyin"></div>
      <div class="search">
        <input type="text" placeholder="" v-model="searchXc_value">
        <button type="button" @click="select3"><img src="static/search.png" alt=""></button>
      </div>
      <div class="selectbar">
        <div class="noselect">
          <ul>
            <span class="ultitle">:</span>
            <li :class="{liactive: selectbar1 === ''}" @click="select1('')"></li>
            <li :class="{liactive: selectbar1 === ''}" @click="select1('')"></li>
            <li :class="{liactive: selectbar1 === ''}" @click="select1('')"></li>
            <li :class="{liactive: selectbar1 === ''}" @click="select1('')"></li>
          </ul>
          <ul>
            <span class="ultitle">:</span>
            <li :class="{liactive: selectbar2 === ''}" @click="select2('')"></li>
            <li :class="{liactive: selectbar2 === ''}" @click="select2('')"></li>
            <li :class="{liactive: selectbar2 === ''}" @click="select2('')"></li>
            <li :class="{liactive: selectbar2 === ''}" @click="select2('')"></li>
          </ul>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="bodybar" id="bodybar">
      <div class="body2 clear">
        <transition-group name="slide-fade">
          <div class="cell" v-for="(xc, index) in country" :key="index">
            <div class="cellimg">
              <img class="transition3" :src="'img/countryside/mainPic/' + xc.mianpic">
            </div>
            <div class="cellyin"></div>
            <div class="celltext transition3">
              {{xc.name}}
            </div>
            <div class="celltext2 transition3">
              <router-link :to="{path: '/show/countryside', query: { id: xc.id }}" target="_blank">
                <img src="static/search.png" alt="">
              </router-link>
            </div>
            <div class="celltext3">
              <span class="biaoqian">{{xc.countrytype}}</span>
            </div>
          </div>
        </transition-group>
      </div>
      <!--  -->
      <div class="waitbar" v-show="showWait">
        <img src="static/wait.gif">
      </div>
      <div class="waitbar" v-show="!showWait">
        {{showWaitText}}
      </div>
    </div>
    <!--  -->
    <div class="backToTop transition1 rotateY" @click="scrollUpFun()">
      <img src="static/toup2.png">
    </div>
    <!--  -->
    <!--<div style="position: fixed;top: 50px;left: 50px;width: 50px;height: 100px;background: white">
      {{scroll}}<br>
      {{getXcIndex}}<br>
      {{scrollIndex}}<br>
      {{country.length}}<br>
    </div>-->
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /*  */
        //   600px
        scrollIndex: 600,
        // 
        scroll: 0,
        // getXc
        doGetXc: true,
        /*  */
        //  
        showWait: false,
        /*  */
        // 
        selectbar1: '',
        selectbar2: '',
        searchXc_value: '',
        // *6
        getXcIndex: 0,
        showWaitText: '~~~',
        /*  */
        // 
        country: [],
        /*  */
        //  
        sxValue: {
          type1: '',
          type2: '',
          page: 0,
          key: ''
        }
        /*  */
      }
    },
    methods: {
      /* url */
      GetQueryString (name) {
        var reg = new RegExp('(\\\\?|\\\\&)' + name + '=([^\\\\&]+)')
        var reg2 = new RegExp('([^=]+)$')
        var r = window.location.href.match(reg)
        if (r != null) {
          return r[0].match(reg2)[0]
        } else {
          return null
        }
      },
      // 
      getXc () {
        this.doGetXc = false
        // 
        this.sxValue.type1 = this.selectbar1
        this.sxValue.type2 = this.selectbar2
        this.sxValue.page = this.getXcIndex
        this.sxValue.key = this.searchXc_value
        console.log(',')
        console.log(this.sxValue)
        this.showWait = true
        const self = this
        axios.post('country/ByType', self.sxValue, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }).then(function (res) {
          console.log('')
          console.log(res)
          //  county
          if (self.getXcIndex === 0) {
            self.country = []
            self.showWaitText = '~~~'
          }
          //  
          if (res.data.length === 0 && self.getXcIndex === 0) {
            self.country = []
            self.scrollIndex = 2000
            self.showWaitText = '~~~'
            console.log('')
            self.getXcIndex++
          } else if (res.data.length === 0){
            // 
            self.scrollIndex = 2000
            console.log(self.getXcIndex + '')
            self.getXcIndex++
          } else if (res.data.length < 6){
            // 6 country
            self.scrollIndex = 2000
            console.log(self.getXcIndex + '6')
            self.getXcIndex++
            self.country = self.country.concat(res.data)
          } else {
            // 6 country
            console.log(self.getXcIndex + '6')
            self.getXcIndex++
            self.country = self.country.concat(res.data)
          }
          self.showWait = false
          self.doGetXc = true
        }).catch(function (err) {
          console.log('' + err)
          self.scrollIndex = 2000
          self.getXcIndex++
          self.showWait = false
          self.showWaitText = '~~~'
          self.doGetXc = true
        })
      },
      // 1
      select1 (str) {
        this.selectbar1 = str
        this.getXcIndex = 0
        this.scrollIndex = 600
        this.country = []
        this.getXc()
      },
      // 2
      select2 (str) {
        this.selectbar2 = str
        this.getXcIndex = 0
        this.scrollIndex = 600
        this.country = []
        this.getXc()
      },
      // 3
      select3 () {
        this.getXcIndex = 0
        this.scrollIndex = 600
        this.country = []
        this.getXc()
      },
      // 
      menu () {
        //  scrollIndex
        this.scroll = document.documentElement.scrollTop || document.body.scrollTop
        if (this.doGetXc) {
          console.log('' + this.scroll + ' ' + this.getXcIndex*this.scrollIndex + ',value:' + this.doGetXc)
          if (this.scroll >= (this.getXcIndex * this.scrollIndex) && this.getXcIndex > 0) {
            this.getXc()
          }
        }
        /*  */
        var btnBacktop = document.getElementsByClassName('backToTop')[0]
        if (this.scroll < 500) {
          btnBacktop.style.opacity = '0'
        } else {
          btnBacktop.style.opacity = '1'
        }
      },
      // 
      scrollUpFun () {
        console.log('')
        const self = this
        var n = this.scroll - Math.floor(this.scroll / 10) * 10
        var timer = setInterval(function () {
          if (self.scroll > n) {
            window.scrollTo(self.scroll, self.scroll - 10)
            self.scroll -= 10
          } else {
            if (self.scroll > 0) {
              window.scrollTo(n, 0)
            }
            clearInterval(timer)
          }
        }, 1)
      }
    },
    created () {
      this.getXc()
      if (this.GetQueryString('key')) {
        this.key = decodeURI(this.GetQueryString('key'))
        this.searchXc_value = decodeURI(this.GetQueryString('key'))
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
    }
  }
</script>

<style scoped lang="stylus">
  @import "../../styles/style.css";
  @import "../../styles/move.css";
  .warp{
    width: 100%;
    height: auto;
    background: #f5f5f5;
  }
  .topbar{
    width: 100%;
    height: 400px;
    overflow: hidden;
    position: relative;
  }
  .topimg{
    overflow: hidden;
    width: 100%;
    position: absolute;
  }
  .topbar .topimg img {
    width: 100%;
  }
  .topyin{
    width: 100%;
    height: 100%;
    position: absolute;
    background: rgba(0,0,0,0.5);
  }
  .search{
    width: 430px;
    height: 50px;
    position: absolute;
    border: 2px solid #FFDE3A;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
  .search input{
    background: transparent;
    border: 0px solid white;
    height: 50px;
    line-height: 50px;
    width: 380px;
    box-sizing: border-box;
    margin: 0px;
    padding: 0px 0px 0px 15px;
    display: block;
    float: left;
    outline: none;
    color: white;
  }
  .search button{
    width: 50px;
    height: 50px;
    background: #FFDE3A;
    border: 0px solid white;
    box-sizing: border-box;
    display: block;
    float: left;
    outline:none;
  }
  .selectbar{
    height: 100px;
    position: absolute;
    left: 50%;
    top: 75%;
    transform: translate(-50%,-50%);
  }
  .selectbar ul{
       width: auto;
       height: 50px;
       line-height: 50px;
       padding: 0px 0px;
       clear: both;
     }
  .selectbar ul li{
    width: auto;
    height: 30px;
    line-height: 30px;
    padding: 0px 5px;
    margin: 10px 5px;
    float: left;
    cursor: pointer;
    color: #999;
  }
  .selectbar ul .liactive{
    border-bottom: 2px solid #FFDE3A;
    color: #fff;
  }
  .selectbar ul .ultitle{
    width: 60px;
    height: 30px;
    line-height: 30px;
    margin: 10px 0px;
    text-align-last: justify;
    padding-right: 10px;
    /*border-right: 2px solid #999;*/
    display: block;
    float: left;
    color: #999;
  }
  .bodybar{
    width: 100%;
    height: auto;
    padding-bottom: 75px;
  }
  .bodybar .body2{
    width: 1100px;
    height: auto;
    min-height: 200px;
    margin: 10px auto 0px;
    clear: both;
    padding: 30px 40px;
    /*border-top: 2px solid #cccccc;*/
    box-sizing: border-box;
    overflow: hidden;
    display: flex;
    justify-content: space-between;
  }
  .bodybar .body2 .cell{
    width: 300px;
    height: 290px;
    float: left;
    margin: 20px;
    position: relative;
    box-shadow: #ccc 1px 1px 3px 2px;
  }
  .bodybar .body2 .cell .cellimg{
    width: 300px;
    height: 250px;
    overflow: hidden;
    position: absolute;
  }
  .bodybar .body2 .cell .cellimg img{
    width: auto;
    height: 120%;
    margin-left: -25px;
    margin-top: -25px;
  }
  .bodybar .body2 .cell .cellyin {
    width: 100%;
    height: 250px;
    position: absolute;
    background: rgba(0, 0, 0, 0.6);
    opacity: 0;
    transition: opacity 0.3s;
  }
  .bodybar .body2 .cell .celltext{
    width: 300px;
    height: 50px;
    position: absolute;
    text-align: center;
    top: 40%;
    transform: translateY(-50%);
    color: #fff;
    font-size: 26px;
    font-weight: bold;
    line-height: 50px;
    letter-spacing:7px;
  }
  .bodybar .body2 .cell .celltext2{
    width: 32px;
    height: 32px;
    position: absolute;
    top:58%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: #fff;
    line-height: 50px;
    opacity: 0;
    transition: All 0.4s ease-in-out;
    cursor: pointer;
  }
  .bodybar .body2 .cell .celltext3{
    width: 100%;
    height: 40px;
    line-height: 40px;
    color: #666;
    position: absolute;
    bottom: 0px;
    left: 0px;
    cursor: pointer;
    background: #fff;
    padding-left: 20px;
    box-sizing: border-box;
  }
  .bodybar .waitbar{
    width: 100%;
    height: 75px;
    line-height: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #999;
  }
  .bodybar .waitbar img{
    width: 100px;
    height: 20px;
  }
  .biaoqian{
    font-size: small;
    width: auto;
    display: block;
    padding: 0px 5px;
  }
  .bodybar .body2 .cell:hover .cellyin{
    opacity: 1;
  }
  .bodybar .body2 .cell:hover .cellimg img{
    transform: scale(0.95);
  }
  .bodybar .body2 .cell:hover .celltext{
    transform: translateY(-50px);
  }
  .bodybar .body2 .cell:hover .celltext2{
    transform: translate(-50%,-50px);
    opacity: 1;
  }

  .backToTop{
    width: 60px;
    height: 60px;
    position: fixed;
    bottom: 150px;
    right: 40px;
    opacity: 0;
  }
  .backToTop img{
    width: 100%;
    height: 100%;
    cursor: pointer;
  }
</style>



