<template>
	<div>
	  <router-view :message="message"></router-view>
      <div class="footer">
	     2017 ZheJiang. All Rights Reserved | Design by Us.
	  </div>
	</div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        message: {
          userName: '',
          userPic: '',
          // 
          doAxios2: true
        }
      }
    },
    methods: {
      getUser () {
        const self = this
        /* 1 */
        axios.post('CountrysideUser/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }
        ).then(function (res) {
          if (res.data === '') {
            console.log('show,')
          } else {
            // 
            self.message.doAxios2 = false
            axios.get('CountrysideUser/info'
            ).then(function (res) {
              console.log('show')
              self.message.userName = res.data.countrysideUser[0].name
              self.message.userPic = res.data.countrysideUser[0].headpic
            }
            ).catch(function (err) {
              console.log('show' + err)
            })
          }
        }
        ).catch(function (err) {
          console.log('show,404' + err)
        })
        if (this.message.doAxios2) {
          //  
          axios.post('user/isLogin', {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
          }
          ).then(function (res) {
            if (res.data === '') {
              console.log('show,')
            } else {
              // 
              axios.get('user/info').then(function (res) {
                console.log('show')
                self.message.userName = res.data.user[0].username
                self.message.userPic = 'img/headpic/' + res.data.user[0].headpic
              }).catch(function (err) {
                console.log('show' + err)
              })
            }
          }
          ).catch(function (err) {
            console.log('show,404' + err)
          })
        }
      }
    },
    created () {
      this.getUser()
    }
  }
</script>
<style scoped>
.footer{
  width: 100%;
  height: 120px;
  background: #000;
  color: #fff;
  text-align: center;
  line-height: 120px;
}
</style>



