<template>
	<div>
		<div class="contain">
			<div class="hg1"></div>
			<form>
				<ul>
					<li class="input_til">&ensp;&ensp;</li>
					<li><input type="text" v-model="user.name" class="input_con input_conl" @change="judge('username')"></li>
					<li class="error_notice" v-show="showNotice_name">10</li>
				</ul>
				<ul>
					<li class="input_til"></li>
					<li><input type="text"  class="input_con input_conl" v-model="user.phonenum" @change="judge('phone')"></li>
					<li class="error_notice" v-show="showNotice_phone"></li>
				</ul>
				<ul>
					<li class="input_til">&emsp;&emsp;</li>
					<li><input type="text"  class="input_con input_conl" v-model="user.email" @change="judge('email')"></li>
					<li class="error_notice" v-show="showNotice_email"></li>
				</ul>
				<ul>
					<li class="input_til">&emsp;&emsp;</li>
					<li><label class="ut_choose" @click="changeType(0)"><input name="userType" type="radio" value="" checked="true"/>&ensp;</label></li>
					<li><label class="ut_choose" @click="changeType(1)"><input name="userType" type="radio" value="" />&ensp;</label></li> 
				</ul>
				<ul>
					<li class="input_til">&emsp;&emsp;</li>
					<li><input type="password"  class="input_con input_conl" v-model="user.passwordO"  @keydown="judge('password')"></li>
					<li class="error_notice" v-show="showNotice_password">6-16()</li>
				</ul>
				<ul>
					<li class="input_til"></li>
					<li><input type="password"  class="input_con input_conl" v-model="passwordS" @change="judge('password2')"></li>
					<li class="ma_notice" v-show="showNotice"></li>
				</ul>
				<ul>
					<li class="input_til">&ensp;&ensp;</li>
					<li><input type="text"  class="input_con input_cons" v-model="user.yanzhen"></li>
					<li class="ma_con"><img alt="" id="" :src="yanzhensrc"/></li>
					<li class="ma_notice"><span @click="reloadCode"></span></li>
				</ul>
				<ul>
					<li><input type="reset" class="res" value=""></li>
					<!-- @click.prevent="changeAndTurn()"  -->
					<li><input type="submit" class="sub" value="" @click.prevent="postData()"></li>
				</ul>
			</form>
		</div>
		<!-- alert -->
		<div class="modal" v-if="showModalValue === 'modal-alter'">
			<div class="modal-alter">
				<div class="modal-alter-text">
          {{noticeText}}
				</div>
				<div class="modal-alter-btn">
					<!-- <button class="alter-btn-close" @click="closeBtn()"></button> -->
					<button class="alter-btn-close">
						<a :href="aHref" class="link-btn" @click="closeBtn()"></a>
					</button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        user: {
          name: '',
          phonenum: '',
          email: '',
          passwordO: '',
          yanzhen: '',
          type: '0'
        },
        passwordS: '',
        showNotice: false,
        showNotice_phone: false,
        showNotice_password: false,
        showNotice_email: false,
        showNotice_name: false,
        yanzhensrc: 'servlet/ImageServlet',
        showModalValue: '',
        noticeText: '',
        turnOrNot: false,
        aHref: 'javascript:;'
      }
    },
    methods: {
      judge (str) {
        if (str === 'password2') {
          if (this.user.passwordO !== this.passwordS) {
            this.showNotice = true
          } else {
            this.showNotice = false
          }
        } else if (str === 'phone') {
          if (/^[0-9]{11}$/.test(this.user.phonenum)) {
            this.showNotice_phone = false
          } else {
            this.showNotice_phone = true
          }
        } else if (str === 'password') {
          if (/^([a-zA-Z0-9]|[_]){5,20}$/.test(this.user.passwordO)) {
            this.showNotice_password = false
          } else {
            this.showNotice_password = true
          }
        } else if (str === 'email') {
          if (/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((\.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(this.user.email)) {
            this.showNotice_email = false
          } else {
            this.showNotice_email = true
          }
        } else if (str === 'username') {
          if (/^[\u4e00-\u9fa5\dA-Za-z_]{1,10}$/.test(this.user.name)) {
            this.showNotice_name = false
          } else {
            this.showNotice_name = true
          }
        }
      },
      postData () {
				if (this.user.passwordO.length < 6){
					this.showNotice_password = true
					return
				}
				var url = ''
				if (this.user.type === '0') {
					url = 'user/register'
				} else {
					url = 'CountrysideUser/register'
				}
				const self = this
        axios.post(url, self.user, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
        }).then(function (data) {
          console.log(data)
          if (data === '') {
						self.noticeText = ''
						self.turnOrNot = true
						self.aHref = '#/loadandreg/load'
          } else {
						self.noticeText = data
          }
          self.showModalValue = 'modal-alter'
          // console.log(data.data)
          if (data.data.errno === 0) {
            console.log('')
          }
					self.reloadCode()
        }).catch(function (err) {
          console.log(err)
          self.noticeText = ''
          self.showModalValue = 'modal-alter'
					self.reloadCode()
        })
      },
      reloadCode () {
        var time = new Date().getTime()
        this.yanzhensrc = 'servlet/ImageServlet?d=' + time
      },
      changeType (str) {
        this.user.type = str
        // console.log(str)
      },
      closeBtn () {
        if (!this.turnOrNot) {
          this.showModalValue = ''
        }
      }
    }
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import '../../styles/model.css';

.hg1{
	width: 36px;
	height: 3px;
	background: #FFDE3A;
	margin: -2px 76px;
}
.contain{
	width: 94%;
	/*height: 320px;*/
	margin: 0px 3%; 
	/*background: rgba(111,222,55,0.8);*/
}
form{
	width: 800px;
	height: 300px;
	margin: 20px auto 0px;
	/*background: rgba(111,122,55,0.8);*/
}
form ul{
	width: 600px;
	height: 50px;
	margin:0px 0px 0px 200px;
	/*background: red;*/
}
form ul li{
	float: left;
}
.input_til{
	color: #fff;
	height: 50px;
	line-height: 50px;
	margin-right: 15px;
}
.input_con{
	border: 1px solid rgba(140,140,140,0.2);
	border-radius: 5px;
	background: rgba(140,140,140,0.4);
	height: 34px;
	color: #fff;
	margin-top: 6px;
	line-height: 34px;
	padding-left: 5px;
}
.input_conl{
	width: 300px;
}
.input_cons{
	width: 100px;
}
.ma_con{
  width: 100px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.ma_con img{
  width: 80px;
  height: 22px;
}
.ma_notice,.error_notice{
	/*color: rgba(165,165,165,0.9);*/
	color: rgba(220,0,0,0.9);
	height: 50px;
	line-height: 50px;
	font-size: 12px;
}
.ma_notice span{
	color: rgba(165,165,165,0.9);
	cursor: pointer;
}
.res,.sub{
	width: 70px;
	height: 30px;
	font-size: 12px;
	background: #FFDE3A;
	border-radius: 3px;
	border: 1px solid #FFDE3A;
	font-family: '';
	cursor: pointer;
	margin-top: 20px;
}
.res{
	margin: 20px 30px 0px 230px;
}
textarea{
	font-family: '';
	font-size: 16px;
}
.ut_choose{
	height: 50px;
	line-height: 50px;
	margin-right: 30px;
	color: #fff;
	cursor: pointer;
}
</style>



