<script>
	// data
	export default {
		data () {
			return {
				showDay: 0,
				scroll: 0,
				nowFixed: false
			}
		},
		created () {
			this.menun()
		},
		methods: {
			menun () {
				window.scrollTo(0, 0)
			},
			// 
			locatedDay (num1, num2) {
				// 
				this.showDay = num1
				// 
				var divBox = document.getElementsByClassName('which-day')[num1]
				var nowDivTop = divBox.offsetTop - 80
				console.log('' + nowDivTop)
				// 
				let n = Math.floor(nowDivTop / 10) * 10
				let timer = setInterval(() => {
							if (this.scroll < n) {
					window.scrollTo(this.scroll, this.scroll + 10)
					this.scroll += 10
				} else {
					if (this.scroll < nowDivTop) {
						window.scrollTo(this.scroll, nowDivTop)
					}
					clearInterval(timer)
				}
			}, 1)
			},
			menu () {
				this.scroll = document.documentElement.scrollTop || document.body.scrollTop
				console.log(this.scroll)
				if (this.scroll >= 216) {
					this.nowFixed = true
				} else {
					this.nowFixed = false
				}
			}
			// 
			// testFun () {
			//   var titleDiv = document.getElementsByClassName('route-title')[0]
