<template>
  <div>
		<div class="contain">
			<div class="hg1"></div>
			<form>
				<ul>
					<li class="input_til">&ensp;&ensp;</li>
					<li><input type="text" v-model="user.email" class="input_con input_conl"></li>
				</ul>
				<ul>
					<li class="input_til">&emsp;&emsp;</li>
					<li><input type="password" v-model="user.password" class="input_con input_conl"></li>
					<li class="ma_notice"></li>
				</ul>
				<ul>
					<li class="input_til">&ensp;&ensp;</li>
					<li><input type="text" class="input_con input_cons" v-model="user.checkcode"></li>
					<li class="ma_con"><img alt="" id="" :src="yanzhensrc"/></li>
					<li class="ma_notice"><span @click="reloadCode"></span></li>
				</ul>
				<ul>
					<li class="input_til">&emsp;&emsp;</li>
					<li><label class="ut_choose" @click="changeType(0)"><input name="userType" type="radio" value="1" checked="true"/>&ensp;</label></li>
					<li><label class="ut_choose" @click="changeType(1)"><input name="userType" type="radio" value="2" />&ensp;</label></li> 
				</ul>
				<ul>
					<li><input type="reset" class="res" value="" ></li>
					<!-- @click.prevent="postData(user)"  -->
					<li><a class="link-btn"><input type="submit"  class="sub" value="" @click.prevent="postData()"></a></li>
				</ul>
			</form>
		</div>
		<!-- alert -->
		<div class="modal" v-if="showModalValue === 'modal-alter'">
			<div class="modal-alter">
				<!-- <button class="btn-close" @click="showChange('close')"></button> -->
				<!-- <div class="modal-head">
					
				</div> -->
				<div class="modal-alter-text">
          {{noticeText}}
				</div>
				<div class="modal-alter-btn">
					<!-- <button class="alter-btn-close" @click="closeBtn()"></button> -->
					<button class="alter-btn-close">
						<a :href="aHref" class="link-btn" @click="closeBtn()"></a>
					</button>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        user: {
          email: '',
          password: '',
          checkcode: '',
          type: '0'
        },
        yanzhensrc: 'servlet/ImageServlet',
				/* alert */
				showModalValue: '',
				noticeText: '',
        turnOrNot: false,
        aHref: 'javascript:;'
      }
    },
    methods: {
      /*  */
      postData () {
				if (this.user.email === '') {
					this.noticeText = ''
					this.showModalValue = 'modal-alter'
				} else if (this.user.password === '') {
					this.noticeText = ''
					this.showModalValue = 'modal-alter'
				} else if (this.user.checkcode === '') {
					this.noticeText = ''
					this.showModalValue = 'modal-alter'
				} else{
					const self = this
					if (self.user.type === '0') {
						axios.post('user/login', self.user, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
						}).then(function (data) {
							console.log(',')
							console.log(data.data)
							if (data.data === '') {
								self.noticeText = ''
								self.turnOrNot = true
								self.aHref = '#/?get=t'
								console.log(',:' + self.aHref)
							} else {
								self.noticeText = data.data
								console.log('' + self.noticeText)
								self.reloadCode()
							}
							self.showModalValue = 'modal-alter'
							if (data.data.errno === 0) {
								console.log('')
							}
						}).catch(function (err) {
							console.log('404' + err)
							self.noticeText = ''
							self.showModalValue = 'modal-alter'
							self.reloadCode()
						})
					} else {
						axios.post('CountrysideUser/login', self.user, {headers: {'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'}
						}).then(function (data) {
							console.log('')
							console.log(data.data)
							if (data.data === '') {
								self.noticeText = ''
								self.turnOrNot = true
								self.aHref = '#/person/xiangcun?get=t'
								console.log(':' + self.aHref)
							} else {
								self.noticeText = data.data
								console.log('' + self.noticeText)
								self.reloadCode()
							}
							self.showModalValue = 'modal-alter'
							if (data.data.errno === 0) {
								console.log('')
							}
						}).catch(function (err) {
							console.log('404' + err)
							self.noticeText = ''
							self.showModalValue = 'modal-alter'
							self.reloadCode()
						})
					}
				}
      },
			/*  */
      reloadCode () {
        var time = new Date().getTime()
        this.yanzhensrc = 'servlet/ImageServlet?d=' + time
      },
			/*  */
      changeType (str) {
        this.user.type = str
        console.log(str)
      },
			/*  */
      closeBtn () {
        if (!this.turnOrNot) {
          this.showModalValue = ''
        }
      }
    }
  }
</script>
<style scoped>
@import '../../styles/main.css';
@import '../../styles/model.css';

  


.hg1{
	width: 36px;
	height: 3px;
	background: #FFDE3A;
	margin: -2px 20px;
}
.contain{
	width: 94%;
	margin: 0px 3%;
}
form{
	width: 800px;
	height: 300px;
	margin: 20px auto 0px;
	/*background: rgba(111,122,55,0.8);*/
}
form ul{
	width: 600px;
	height: 50px;
	margin:8px 0px 8px 200px;
	/*background: red;*/
}
form ul li{
	float: left;
}
.input_til{
	color: #fff;
	height: 50px;
	line-height: 50px;
	margin-right: 15px;
}
.input_con{
	border: 1px solid rgba(140,140,140,0.2);
	border-radius: 5px;
	background: rgba(140,140,140,0.4);
	height: 34px;
	color: #fff;
	margin-top: 6px;
	line-height: 34px;
	padding-left: 5px;
}
.input_conl{
	width: 300px;
}
.input_cons{
	width: 100px;
}
.ma_con{
	width: 100px;
	height: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.ma_con img{
	width: 80px;
	height: 22px;
}
.ma_notice{
	height: 50px;
	line-height: 50px;
	font-size: 12px;
}
.ma_notice span{
	color: rgba(165,165,165,0.9);
	cursor: pointer;
}
.res,.sub{
	width: 70px;
	height: 30px;
	font-size: 12px;
	background: #FFDE3A;
	border-radius: 3px;
	border: 1px solid #FFDE3A;
	font-family: '';
	cursor: pointer;
	margin-top: 20px;
}
.res{
	margin: 20px 30px 0px 230px;
}
textarea{
	font-family: '';
	font-size: 16px;
}
.ut_choose{
	height: 50px;
	line-height: 50px;
	margin-right: 30px;
	color: #fff;
	cursor: pointer;
}
</style>



