<script>
  export default {
    data () {
      return {
        showStep: 0,
        showWay: 0,
        showLong: 0,
        showPatner: 0,
        showLike: [false, false, false, false, false, false],
        showPlace: '000',
        oneroute: [
          [{
            num: 1, placename: '', type: ''
          }, {
            num: 2, placename: '', type: ''
