        btnText: '',
        scroll: 0,
        checkedNames: ['baicaoyuan','tianhuangpang','huaxuechang','shaokaochang','shaokaochang1'],
        routes: [],
        mapshow: false,
      }
    },
    created () {
      this.menun()
    },
    methods: {
      menun () {
        window.scrollTo(0, 0)
      },
      changeSelect (str, num, locate) {
        if (str === 'step') {
          this.showStep = num
          if (num === 1 && !this.showRoute) {
            console.log('')
          } else {
            // 
            let n = Math.floor(locate / 10) * 10
            let timer = setInterval(() => {
                  if (this.scroll < n) {
              window.scrollTo(this.scroll, this.scroll + 10)
              this.scroll += 10
            } else {
              if (this.scroll < locate) {
                window.scrollTo(this.scroll, locate)
              }
              clearInterval(timer)
            }
          }, 1)
          }
        } else if (str === 'way') {
          this.showWay = num
        } else if (str === 'long') {
          this.showLong = num
        } else if (str === 'patner') {
          this.showPatner = num
        } else if (str === 'like') {
          this.$set(this.showLike, num, !this.showLike[num])
        } else {
          this.showPlace = num
        }
      },
      changeOnePlace (str) {
        this.showPlace = str
        console.log(str)
      },
      showRouteFun () {
        this.showRoute = true
        this.showStep = 1
        this.handleMaps()
      },
      finishMake () {
        this.showStep = 2
        this.btnText = ''
      },
      menu () {
        this.scroll = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        // console.log(this.scroll)
        // if (this.scroll >= 216) {
        //   this.nowFixed = true
        // } else {
        //   this.nowFixed = false
        // }
      },
      handleMaps() {
        if (1) {
          console.log('')
          const self = this;
          this.mapshow = true;
          const baseMap = [119.615765,30.611777]; // 
          // 
          const place = {
            'tianhuangpang': {
              map: [119.615765,30.611777],
              name: ''
            },
            'baicaoyuan': {
              map: [119.6224195741,30.5887256557],
              name: '',
            },
            'huaxuechang': {
              map: [119.595233,30.609507],
              name: ''
            },
            'shaokaochang': {
              map: [119.6174882341,30.6171792990],
              name: ''
            },
            'shaokaochang1': {
              map: [119.6599684451,30.5908850414],
              name: ''
            }
          };

          const lnglat = new AMap.LngLat(...baseMap);
          const maparr = [];

          this.checkedNames.map((item, index) => {
            maparr.push(place[item]);
        });

          for(let i = 0; i < maparr.length; i++) {
            maparr[i]['distance'] =  parseInt(lnglat.distance(maparr[i].map));
            console.log(lnglat.distance(maparr[i].map))
          }

          for(let i = 0; i < maparr.length; i++) {
            for(let j = i; j < maparr.length; j++) {
              if(maparr[i]['distance'] > maparr[j]['distance']) {
                const t  = maparr[i];
                maparr[i] = maparr[j];
                maparr[j] = t;
              }
            }
          }
//
          window.map.clearMap();  // 
          // 
          const markers = maparr.map((item, index, arr) => {
                return {
                  icon: `http://webapi.amap.com/theme/v1.3/markers/n/mark_b${index + 1}.png`,
                  position: item.map
                }
              });
          // ,
          markers.forEach(function (marker) {
            new AMap.Marker({
              map: window.map,
              icon: marker.icon,
              position: [marker.position[0], marker.position[1]],
              offset: new AMap.Pixel(-12, -36)
            });
          });
          // 
          const lineArr = maparr.map((item, index ,arr) => {
                return item.map;
        });

          const polyline = new AMap.Polyline({
            path: lineArr,          //
            strokeColor: "#3366FF", //
            strokeOpacity: 1,       //
            strokeWeight: 5,        //
            strokeStyle: "solid",   //
            strokeDasharray: [10, 5] //
          });
          polyline.setMap(map);

          // 
          const driving = new AMap.Driving();
          // 

          for(let i = 0; i < maparr.length - 1; i++) {
            driving.search(new AMap.LngLat(...maparr[i].map), new AMap.LngLat(...maparr[i+1].map), function (status, result) {
              self.routes.push({
                distance: parseInt(result.routes[0].distance / 10) / 100 + 'Km',
                time: parseInt(result.routes[0].time / 60) + '' ,
              });
            });
          }
        }
      }
    },
    mounted () {
      window.addEventListener('scroll', this.menu)
      const TYPE = '||||';
      const NUMBER = '||';
      const DAYS = '1|2|3|4|5|6|7';
      window.map = new AMap.Map('container', {
        resizeEnable: true,
        center: [119.63763,30.631471], // 
        zoom: 11,
      });
    }
  }
