<template>
  <div>
    <div class="midr">
      <div class="midr1 mid1">
        <span :class="{active: show === 'tab1'}"><a href="javascript:;" @click="showTab('tab1')"></a></span>
        <span :class="{active: show === 'tab2'}"><a href="javascript:;" @click="showTab('tab2')"></a></span>
        <span :class="{active: show === 'tab3'}"><a href="javascript:;" @click="showTab('tab3')"></a></span>
      </div>
      <div class="midr2 content" v-if="show === 'tab1'">
        <ul class="tongzhi tongzhi1 gray">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <ul class="tongzhi tongzhi1" v-for="(shenhe, index) in shenhe" :key="index">
          <li v-text="shenhe.time"></li>
          <li v-text="shenhe.name"></li>
          <li v-text="shenhe.type"></li>
          <li v-text="shenhe.state" :class="showStyle1(shenhe.state, 'color', '', '')"></li>
          <!-- 1.show -->
          <!-- 2.this.stateindex -->
          <!-- 3. -->
          <li><a :href="showStyle1(shenhe.state, 'chakanSrc', shenhe.type, 0)" v-text="showStyle1(shenhe.state, 'chakanText', 0, 0)" @click="sorry"></a></li>
          <li><a href="javascript:;" @click="sorry" v-text="showStyle1(shenhe.state, 'xiugaiText', 0, 0)"></a></li>
        </ul>
      </div>
      <div class="midr3 content" v-if="show === 'tab2'">
        <ul class="tongzhi tongzhi2 gray">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <ul class="tongzhi tongzhi2" v-for="(baocuo, index) in baocuo" :key="index">
          <li v-text="baocuo.time"></li>
          <li><a :href="baocuo.name" v-text="baocuo.name"></a></li>
          <li v-text="baocuo.state" :class="showStyle1(baocuo.state, 'color', '', '')"></li>
          <!-- id -->
          <li><a href="javascript:;" @click="sorry">/</a></li>
        </ul>
      </div>
      <div class="midr4 content" v-if="show === 'tab3'">
        <ul class="tongzhi tongzhi3 gray">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <ul class="tongzhi tongzhi3" v-for="(onebeibaocuo, index) in beibaocuo" :key="index">
          <li v-text="onebeibaocuo.time"></li>
          <li><a :href="onebeibaocuo.name" v-text="onebeibaocuo.name"></a></li>
          <li @click="sorry"></li>
          <li><a href="javascript:;" @click="sorry"></a></li>
          <li v-text="onebeibaocuo.state" :class="showStyle1(onebeibaocuo.state, 'color', '', '')"></li>
        </ul>
      </div>
      <div style="clear:both"></div>
    </div>
    <!---->
    <div class="modal" v-if="showModalValue === 'modal-beibaocuo'">
      <div class="modal-con">
        <button class="btn-close" @click="showChange('close')"></button>
        <div class="modal-head">
          <span></span>
        </div>
        <!-- <div class="model-time"></div> -->
        <div class="modal-body">
          <div class="onetip clear">
            <span class="leftnotice"></span>
            <div class="rightinfor">{{tempBBCI.quetionType}}</div>
          </div>
          <div class="onetip clear">
            <span class="leftnotice"></span>
            <div class="rightinfor">
              <img :src="tempBBCI.picsrc" alt="" />
            </div>
          </div>
          <div class="onetip clear">
            <span class="leftnotice"></span>
            <div class="rightinfor">{{tempBBCI.quetionLink}}</div>
          </div>
          <div class="onetip clear">
            <span class="leftnotice"></span>
            <div class="rightinfor">{{tempBBCI.tipHelp}}</div>
          </div>
        </div>
        <div class="modal-foot">
        </div>
      </div>
    </div>
    <div class="modal" v-if="showModalValue === 'modal-addwrong'">
      <div class="modal-con">
        <button class="btn-close" @click="showChange('close')"></button>
        <div class="modal-head">
          <span></span>
        </div>
        <div class="model-time"></div>
        <div class="modal-body">
          {{addWrongNotice}}
        </div>
        <div class="modal-foot">
        </div>
      </div>
    </div>
    <!-- alert -->
    <div class="modal" v-if="showModalValue === 'modal-alter'">
      <div class="modal-alter">
        <div class="modal-alter-text">
          {{noticeText}}
        </div>
        <div class="modal-alter-btn">
          <button class="alter-btn-close">
            <a :href="aHref" class="link-btn" @click="closeBtn()"></a>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        /*  */
        showModalValue: '',
        /* alert */
        noticeText: '',
        aHref: 'javascript:;',
        show: 'youji',
        show: 'tab1',
        addWrongNotice: '',
        tempBBCI: {
          quetionType: '',
          picsrc: '',
          quetionLink: '',
          tipHelp: ''
        },
        beiBCInfor: [
          {
            quetionType: '1',
            picsrc: 'static/special1.jpg',
            quetionLink: 'http://qif0qenfqeofneq',
            tipHelp: ''
          },
          {
            quetionType: '2',
            picsrc: 'static/special1.jpg',
            quetionLink: 'http://qif0qenfqeofneq',
            tipHelp: ''
          },
          {
            quetionType: '3',
            picsrc: 'static/special1.jpg',
            quetionLink: 'http://qif0qenfqeofneq',
            tipHelp: ''
          },
          {
            quetionType: '4',
            picsrc: 'static/special1.jpg',
            quetionLink: 'http://qif0qenfqeofneq',
            tipHelp: ' '
          }
        ],
        shenhe: [
          {
            time: '2017/3/9',
            name: '',
            type: '',
            state: '',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: '',
            type: '',
            state: '',
            id: '2'
          },
          {
            time: '2017/3/9',
            name: '',
            type: '',
            state: '',
            id: '3'
          },
          {
            time: '2017/3/9',
            name: '',
            type: '',
            state: '',
            id: '4'
          }
        ],
        baocuo: [
          {
            time: '2017/3/9',
            name: 'http://localhost:63342/mlxc/center_tz.html##',
            state: '',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: 'http://localhost:63342/mlxc/center_tz.html##',
            state: '',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: 'http://localhost:63342/mlxc/center_tz.html##',
            state: '',
            id: '1'
          }
        ],
        beibaocuo: [
          {
            time: '2017/3/9',
            name: '',
            state: '',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: '',
            state: '',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: '',
            state: '',
            id: '1'
          },
          {
            time: '2017/3/9',
            name: '',
            state: '',
            id: '1'
          }
        ]
      }
    },
    methods: {
      /* alert1 */
      closeBtn () {
        this.showModalValue = ''
      },
      /* alert2 */
      alertModal (alertText, aHref) {
        this.noticeText = alertText
        this.aHref = aHref ? aHref : 'javascript:;',
            this.showModalValue = 'modal-alter'
      },
      showTab (str) {
        this.show = str
      },
      showChange (obj, index) {
        if (obj === 'modal-beibaocuo') {
          this.showModalValue = 'modal-beibaocuo'
          this.tempBBCI = this.beiBCInfor[index]
        } else if (obj === 'modal-addwrong') {
          this.showModalValue = 'modal-addwrong'
        } else {
          this.showModalValue = ''
        }
      },
      showStyle1 (state, event, type, obj) {
        if (state === '' || state === '') {
          if (event === 'color') {
            return 'green'
          } else if (event === 'chakanSrc') {
            if (type === '') {
              return '/show/xc?id='
            } else if (type === '') {
              return '/show/jd'
            } else if (type === '') {
              return '/show/cg'
            } else if (type === '') {
              return '/show/zs'
            }
          } else if (event === 'chakanText') {
            return ''
          } else if (event === 'xiugaiText') {
            return ' '
          }
        } else if (state === '' || state === '') {
          if (event === 'color') {
            return 'red'
          } else if (event === 'chakanSrc') {
            return 'javascript:;'
            /*  */
          } else if (event === 'chakanText') {
            return ''
          } else if (event === 'xiugaiText') {
            return ''
          }
        } else {
          if (event === 'color') {
            return 'gray'
          } else if (event === 'chakanSrc') {
            return 'javascript:;'
            /*  */
          } else if (event === 'chakanText') {
            return ''
          } else if (event === 'xiugaiText') {
            return ''
          }
        }
      },
      getDataShenghe () {
        axios.get('shenheInfo').then(function (res) {
          console.log(' ')
          this.shenhe = res.data.shenhe
          console.log(res.data)
        }).catch(function (err) {
          console.log(' ')
          console.log(err)
        })
      },
      /*  */
      sorry () {
        this.alertModal(',')
      }
    }
  }
</script>

<style scoped>
  @import "../../styles/style.css";
  @import '../../styles/model.css';
  .midr  .tongzhi{
    list-style: none;
    width: 100%;
    height:40px;
    line-height: 40px;
  }
  .midr .tongzhi li{
    width:15%;
    float: left;
    display: block;
    text-align: center;
  }
  .midr .tongzhi li:nth-child(2){
    width: 25%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-right: 5px;
    box-sizing: border-box;
    text-align:left;
  }
  .midr .tongzhi li:nth-child(1){
    text-align: left;
  }

  /*  */
  .onetip{
    width: 100%;
    /* background: blueviolet; */
    margin-bottom: 30px;
  }
  .leftnotice{
    float: left;
    width: 15%;
    text-align: right;
    height: 25px;
    line-height: 25px;
    margin-right: 20px;
  }
  .rightinfor{
    line-height: 25px;
    float: left;
    width: 79%;
    text-align: left;
  }
  .rightinfor img{
    height: 200px;
    width: 300px;
  }

</style>



