        axios.post('scenicspot/addScenicspot', self.paramDataInfo, {
          headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (res) {
          if (res.data.info === 'success') {
            self.alertModal('')
          }
          console.log(res)
        }).catch(function (err) {
          console.log('')
          console.log(err)
        })
        console.log('')
        console.log('------')
        console.log(this.paramDataInfo.get('type'))
        console.log('------')
        console.log(this.paramDataInfo.get('name'))
        console.log('------')
        console.log(this.paramDataInfo.get('jianjie'))
        console.log('------')
        console.log(this.paramDataInfo.get('location'))
