            this.dataQianTai.images += ','
          }
        }
        // 
        this.dataQianTai.location = '   ' + this.lishuxian + ' ' + this.lishuxiang
        console.log('')
        console.log('------')
        console.log(this.dataQianTai.name)
        console.log('------')
        console.log(this.dataQianTai.jianjie)
        console.log('------')
        console.log(this.dataQianTai.location)
        console.log('------')
        console.log(this.dataQianTai.price)
