      //  
      getOldData (id, url, dataname) {
        console.log('' + id + ', ' + url + ', ' + dataname)
        const self = this
        axios.post(url, id, {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=utf-8'
          }
        }).then(function (res) {
          console.log('')
          var fData_jd = self.dataQianTai
          var aData_jd = res.data[dataname]
          console.log(dataname)
          if (dataname === 'ScenicSpots') {
            fData_jd.name = aData_jd.name
            fData_jd.jianjie = aData_jd.synosis
            fData_jd.location = aData_jd.location
            fData_jd.price = aData_jd.price
            fData_jd.phone = aData_jd.cinformation
            fData_jd.adress = aData_jd.address
            fData_jd.fengmian = aData_jd.cover
            fData_jd.images = aData_jd.pices
            fData_jd.video = aData_jd.video
          } else if (dataname === 'restaurants') {
            fData_jd.name = aData_jd.name
            fData_jd.jianjie = aData_jd.synosis
            fData_jd.location = aData_jd.location
            fData_jd.price = aData_jd.perprice
            fData_jd.phone = aData_jd.cinformation
            fData_jd.adress = aData_jd.address
            fData_jd.fengmian = aData_jd.cover
            fData_jd.images = aData_jd.pices
            fData_jd.video = aData_jd.video
          } else if (dataname === 'hotels') {
            fData_jd.name = aData_jd.hotelname
            fData_jd.jianjie = aData_jd.introduce
            fData_jd.location = aData_jd.location
            fData_jd.price = aData_jd.lowprice
            fData_jd.phone = aData_jd.phone
            fData_jd.adress = aData_jd.detaillocation
            fData_jd.fengmian = aData_jd.mainpic
            fData_jd.images = aData_jd.pice
            fData_jd.video = aData_jd.video
          } else {
            console.log(dataname + ' ~')
          }
          for (var i = 0; i < res.data.ScenicSpotslength; i++) {
            self.myJingdian[i].jdTime = self.dateFormat(self.myJingdian[i].jdTime)
          }
        }).catch(function (err) {
          console.log('~')
        })
